.new-card-item {
    @include flexbox();
	@include flex-direction(column);
    align-items: flex-start;
    // justify-content: flex-end;
    width: 100%;
    min-height: calc(100vw / 4);
    // padding: calc(#{$space-tiny} * 2);
    // padding-top: 6rem;
    text-align: left;
    background: lighten($grey-lt, 4%);
    border: 1px solid lighten($grey-lt, 2%);
    border-top: none;
    transition: $ease;

    span.new-card-number {
        @include font-size(36px);
        @include line-height(1);
        @include title(bold);
        display: block;
        width: 64px;
        height: 64px;
        margin-bottom: 1.5rem;
        padding: 15px 0 0;
        text-align: center;
        color: $blue-dk;
        background: $white;
        border-radius: 50%;
    }

    h2 {
        // @include font-size(22px);
        margin-bottom: calc(#{$space-tiny} * 0.5);
        text-align: left;
        color: $blue-lt;
    }

    h3 {
        @include font-size(22px);
        text-align: left;
        margin-bottom: calc(#{$space-tiny} * 0.5);
        color: $blue-dk;
    }

    h6 {
        margin-bottom: calc(#{$space-tiny} * 0.5);
        color: git add$grey-mdk;
    }

    p {
        @include title(medium);
        @include font-size(16px);
        @include line-height(1.35);
        display: block;
        color: lighten($grey-mdk, 10%);
    }
}

