    }

    @include break-point(tablet) {
        width: calc(calc(100% - 48px) / 2);

        // override mobile
        &:not(:first-child) {
            margin-top: unset;
        }

        &:not(:nth-child(2n+1)) {
            margin-left: auto;
        }

        &:nth-child(n+3) {
            margin-top: 48px;
		}
