.new-card-item-third {
    &:not(:first-child) {
        margin-top: calc(#{$space-tiny} * 2);
    }

    @include break-point(tablet) {
        width: calc(calc(100% - 48px) / 2);

        // override mobile
        &:not(:first-child) {
            margin-top: unset;
        }

        &:not(:nth-child(2n+1)) {
            margin-left: auto;
        }

        &:nth-child(n+3) {
            margin-top: 48px;
		}
	}

	@include break-point(laptop) {
        width: calc(calc(100% - 96px) / 3);

        // override mobile, tablet
        &:not(:first-child),
        &:nth-child(n+3) {
			margin-top: unset;
            margin-left: unset;
		}

        &:not(:nth-child(3n+1)) {
            margin-left: auto;
            margin-left: calc(96px / 3);
        }

        &:nth-child(n+4) {
            margin-top: 48px;
		}

        &:last-child:nth-child(3n+2) {
            margin-left: 48px;
            margin-right: auto;
        }
	}
}

.new-card-item-quarter {
    &:not(:first-child) {
        margin-top: calc(#{$space-tiny} * 2);
    }

    @include break-point(tablet) {
        width: calc(calc(100% - 48px) / 2);

        // override mobile
        &:not(:first-child) {
            margin-top: unset;
        }

        &:not(:nth-child(2n+1)) {
            margin-left: auto;
        }

        &:nth-child(n+3) {
            margin-top: 48px;
		}
	}

	@include break-point(laptop) {
        width: calc(calc(100% - 144px) / 4);

        // override mobile, tablet
        &:not(:first-child),
        &:nth-child(n+4) {
			margin-top: unset;
            margin-left: unset;
		}

        &:not(:nth-child(4n+1)) {
            margin-left: auto;
            margin-left: calc(144px / 3);
        }

        &:nth-child(n+5) {
            margin-top: 48px;
		}

        &:last-child:nth-child(4n+2) {
            margin-left: 48px;
            margin-right: auto;
        }
	}
}

