.section-interrupter .interrupter-image {
    max-height: unset;
    margin-bottom: 0;
    box-shadow: 0 0px 16px 8px rgba(0,0,0,0.1);
    overflow: unset;
}

.interrupter-bg {
    position: absolute;
    top: 0;
    right: 0;
    // bottom: 0;
    // left: 0;
    display: block;
    width: 100%;
    height: 100%;
    margin-top: -2rem;
    margin-right: -2rem;
    content: '';
    z-index: -1;
}

.interrupter-image figcaption {
    position: absolute;
    bottom: -2rem;
    left: -2rem;
}

.interrupter-number {
    @include font-size(80px);
    @include line-height(1);
    @include title(bold);
    display: block;
    width: 10rem;
    height: 10rem;
    margin-top: 1rem;
    padding: 40px 0 0;
    text-align: center;
    color: $white;
    border-radius: 0;
    box-shadow: 0 0px 16px 8px rgba(0,0,0,0.1);
    cursor: pointer;
}

.interrupter-text {
    position: relative;
    max-width: 768px;
    margin: 4rem auto 5rem;
    margin-top: -3rem;
    padding: 3rem 4rem;
    background: $white;
    box-shadow: 0 0px 16px 8px rgba(0,0,0,0.1);
}







