    &:nth-child(6) {
        .interview-number,
        .interview-title {
            background: lighten($orange-dk, 10%);
        }
    }
}

.interview-number {
    @include font-size(32px);
    @include line-height(1);
    @include title(regular);
    display: block;
    width: 64px;
    height: 64px;
    margin-top: 1rem;
    // margin-bottom: 1.5rem;
    padding: 18px 0 0;
    // width: 96px;
    // height: 96px;
    // padding: 32px 0 0;
    text-align: center;
    color: $blue-dk;
    color: $white;
    background: $white;
    background: $grey-lt;
    border-radius: 50%;
    cursor: pointer;
}

.interview-heading {
    @include flexbox();
	@include flex-direction(column);
    display: none;
    max-width: 480px;
    // max-width: 640px;
    // margin-top: 1.5rem;
    // margin-left: 1.5rem;
    margin: 1.5rem 2rem 0 1.5rem;
    // padding: 1.5rem;
    border-radius: 8px;
}

.interview-title {
    @include font-size(22px);
    text-align: left;
    margin-bottom: calc(#{$space-tiny} * 0.5);
    color: $blue-dk;

    padding: .65rem 1rem;
    margin-bottom: 0;
    background: wheat;
    color: $white;
    border-radius: 8px 8px 0 0;
}

.interview-description {
    display: block;
    @include title(medium);
    @include font-size(14px);
    @include line-height(1.5);
    color: lighten($grey-mdk, 10%);

    padding: 0.65rem 1rem 1rem;
    padding: 1rem;
}

.interview-notch {

}

.interview-timeline {
    position: absolute;
    left: 0;
    top: 48px;
    width: 100%;
    height: 4px;
    background: $grey-lt;
    border-radius: 4px;
}


@for $step from 1 through 5 {
    input[id="interview-step-#{$step}"]:checked ~ label[for="interview-step-#{$step}"] .interview-number {
        @include title(bold);
        width: 140px;
        height: 140px;
        margin-top: -22px;
        font-size: 52px;
        padding-top: 44px;

        .interview-step:not(:first-of-type) & {
            margin-left: 1.5rem;
        }
    }

    input[id="interview-step-#{$step}"]:checked ~ label[for="interview-step-#{$step}"] .interview-heading {
        display: block;
        background: $grey-lt;
    }

    // .interview-step:hover input[id="interview-step-#{$step}"]:checked ~ label[for="interview-step-#{$step}"] .interview-heading {
    //     .interview-number,
    //     .interview-title {
    //         background: initial;
    //     }
    // }
}







label.opportunity-label {
    @include title(medium);
    @include font-size(14px);
    @include line-height(1);
    position: relative;
	display: block;
    margin-bottom: 0;
    padding: 12px 16px 12px;
	text-align: left;
	text-transform: uppercase;
    color: $grey-mdk;
    background: lighten($grey-lt, 4%);
    border-radius: 1px;
    cursor: pointer;

    &:not(:first-of-type) {
        margin-top: 0.75rem;
        margin-left: 0;
    }

	&:hover {
        color: darken($grey-mdk, 2.5%);
        background: lighten($grey-lt, 2%);
	}

    @include break-point(tablet) {
	    &:not(:first-of-type) {
            margin-top: 1.5rem;
            margin-left: 0;
        }
    }

    @include break-point(laptop) {
	    @include font-size(15px);
        display: inline-block;
        margin-bottom: 0;
        text-align: center;

        &:not(:first-of-type) {
            margin-top: 0;
            margin-left: 1.5rem;
        }
    }
}

.opportunity-label::after {
    position: absolute;
    bottom: -10px;
    left: 50%;
    margin-left: -10px;
    content: '';
    width: 0;
    height: 0;
    // border-left: 10px solid transparent;
    // border-right: 10px solid transparent;
    // border-top: 10px solid lighten($grey-lt, 4%);
}

.opportunities {
    // padding: 3rem;
    // background: $grey-lt;
}

.opportunity {
    @include flexbox();
	@include flex-direction(column);
    align-items: center;
    width: 100%;
    margin-top: calc(1rem * 1.75);
    background: $grey-lt;
    transition: $ease;
    display: none;

    &:nth-of-type(1) {
        background: $orange-lt;

        h2 {
            color: $blue-dk;
        }

        .button {
            background: $blue-lt;
        }
    }

    &:nth-of-type(2) {
        background: $raspberry;

        h2 {
            color: $orange-lt;
        }

        .button {
            background: $green-lt;
        }
    }

    &:nth-of-type(3) {
        background: $blue-lt;

        h2 {
            color: $orange-lt;
        }

        .button {
            background: $green-lt;
        }
    }

    @include break-point(tablet) {
	    @include flex-direction(row);
        @include flex-wrap(wrap);
    }
}

.opportunity-img {
    order: 1;
    display: block;
    width: 100%;

    @include break-point(laptop) {
        width: 50%;

        .opportunity:nth-child(even) & {
            order: 2;
        }
    }
}

.opportunity-copy {
    order: 2;
    padding: calc(#{$space-tiny} * 1.5);

    p {
        @include font-size(21px);
        @include line-height(1.5);
        color: $white;
    }

    @include break-point(laptop) {
        width: 50%;
        padding: calc(#{$space-tiny} * 2) calc(#{$space-tiny} * 3.5);
    }
}

.opportunity-copy .button {
    margin-top: 1.5rem;
}

@for $opp from 1 through 3 {
    input[id="opportunity-#{$opp}"]:checked ~ .opportunity-#{$opp} {
        @include flexbox();
    }
}

