    background: $raspberry;

    &::after {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 10px solid $raspberry;
    }
}

input[id="opportunity-3"]:checked ~ label[for="opportunity-3"] {
    color: $white;
    background: $blue-lt;

    &::after {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 10px solid $blue-lt;
    }
}



